import Header from "./header"
import React from 'react';
import { useState } from 'react';
import Footer from "./footer.js";
import { useCookies } from "react-cookie";
import  TestItems from "../../TestItems";
import CartItem from "./CartObject";
import '../../Register.css';
import { useNavigate } from "react-router-dom";



import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';

const Cart = () => {

   const [cookies, setCookie, removeCookie] = useCookies(['user','next']);
   const [cart, setCart]=useState(cookies['cart']);
   const redirect = useNavigate()


  function checkout(){
    const topFive = cart.slice(Math.max(cart.length - 5, 0));
    setCookie('lastFive',topFive, 1200, { path: '/' });
    setCookie('history', cart, 1200, { path: '/' });
    removeCookie('cart');
    redirect('/CheckoutSuccess')
  }

  function getInfo(item,value) {
    let productinfo=value.find(element=> item.name===value.name);
    return productinfo.price;
    }



  function ProductList({main,cart}) {
    var filteredCart = cart.filter(item => item.Qty > 0);
    filteredCart=filteredCart.map(obj => ({ ...obj, price:getInfo(cart,main) }));
    const display=filteredCart.map(product => <CartItem key={filteredCart.indexOf(product)} product={product} />);

    return (
      <div>
        {display}
      </div>
    );
  }

  return (

    <div>
      <Header />
      <br />
      <br />
      <br />
      <h2>Cart Page will be fully implemented in the future</h2>

        {cart && (
            <div className="productContainer">
                <ProductList main={TestItems} cart={cookies['cart']} />
            </div>
        )}


        {!cart && (
        <h3>Your cart is Empty</h3>
        )}

      <br />
      <br />
      <button type="button" onClick={checkout}>Checkout</button>
      <br />
      <div className="recurring">
        <Link to="/Recurring">
          <button className="recurring-button" type="button">Recurring Delivery</button>
        </Link>
      </div>
      <br />
    <Footer/>
    </div>
  );

};



export default Cart;