 import React from 'react';
 import '../../Register.css';
import { useCookies } from "react-cookie";
import {useState} from 'react';
import {useContext} from 'react';
import {Context} from '../../CookieHandler';



const Product=({product})=> {
    const [cookies, setCookie] = useCookies(['user','next']);
    const { cart, setCart} = useContext(Context);
    const [qty, setQty]= useState('1')



   function handlesubmit(){
      setCookie('next', {name:product.name,Qty:qty}, { path: '/' });
    }

    function updateCart(){
        if(product.name===cookies['next']){
        cart.push(cookies['next'])}
        else{
        setCookie('next', {'name':product.name,'qty':qty}, { path: '/' });
        cart.push({name:product.name,Qty:qty});

        setCookie('cart', cart, { path: '/' });}
        console.log(cart);
    }

    function submitCart(){
        handlesubmit();
        updateCart();
    }








   return(
     <div className="product">
       <img alt={product.name} src={process.env.PUBLIC_URL+product.imgPath} width="90" height="90" />
         <h3>{product.name}</h3>
         <input type ="number" min='0' width = '3' placeholder='0' onChange={(e) => setQty(e.target.value)}/>
         <button className="search" type="submit" onClick={submitCart}>Add to Cart</button>

     </div>
   );
 }

 export default Product;